"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import UserLayout from "@/components/user/UserLayout";
import useAuthStore from "@/store/authStore";
import useDonationStore from "@/store/donationStore";
import toast from "react-hot-toast";
import Cookies from "js-cookie";
import { BiChevronUp, BiChevronDown } from "react-icons/bi";

export default function MyDonationsPage() {
  const router = useRouter();
  const { user, isAuthenticated, isLoading: authLoading, initializeAuth, getCurrentUser } = useAuthStore();
  const { getUserDonations, userDonations, isLoading } = useDonationStore();
  const [isInitializing, setIsInitializing] = useState(true);
  
  const [filters, setFilters] = useState({
    search: ''
  });
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const token = Cookies.get('token');
        if (token) {
          initializeAuth();
          try {
            await getCurrentUser();
          } catch (error) {
            console.error("Failed to get current user:", error);
          }
        }
      } catch (error) {
        console.error("Auth initialization error:", error);
      } finally {
        setIsInitializing(false);
      }
    };

    checkAuth();
  }, [initializeAuth, getCurrentUser]);

  useEffect(() => {
    if (!isInitializing && !authLoading && !isAuthenticated) {
      toast.error("Please login to view your donations");
      router.push("/login?redirect=/my-donations");
    }
  }, [isAuthenticated, authLoading, isInitializing, router]);

  useEffect(() => {
    if (isAuthenticated && user) {
      getUserDonations();
    }
  }, [isAuthenticated, user, getUserDonations]);

  if (isInitializing || authLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-16 w-16 border-t-4 border-b-4 border-green-500 mx-auto mb-4"></div>
          <p className="text-gray-600 text-lg">Loading...</p>
        </div>
      </div>
    );
  }

  if (!isAuthenticated) {
    return null;
  }

  // Filter donations
  const filteredDonations = userDonations.filter(donation => {
    const matchesSearch = !filters.search || 
      donation.campaign?.title?.toLowerCase().includes(filters.search.toLowerCase()) ||
      (donation.donorDetails?.name || user?.name || '').toLowerCase().includes(filters.search.toLowerCase()) ||
      (donation.donorDetails?.email || user?.email || '').toLowerCase().includes(filters.search.toLowerCase());
    return matchesSearch;
  });

  // Pagination
  const totalPages = Math.ceil(filteredDonations.length / entriesPerPage);
  const startIndex = (currentPage - 1) * entriesPerPage;
  const endIndex = startIndex + entriesPerPage;
  const currentDonations = filteredDonations.slice(startIndex, endIndex);

  return (
    <UserLayout>
      <div className="py-6">
        {/* Heading */}
        <div className="mb-6">
          <h1 className="text-3xl font-bold text-blue-900 mb-2">
            Donation History Data
          </h1>
          <nav className="text-gray-500 text-sm">
            <ol className="flex space-x-2 items-center">
              <li>
                <a href="/dashboard" className="hover:underline text-gray-600 hover:text-blue-600">
                  Home
                </a>
              </li>
              <li className="text-gray-400">/</li>
              <li>
                <a href="/dashboard" className="hover:underline text-gray-600 hover:text-blue-600">
                  Donation Data
                </a>
              </li>
              <li className="text-gray-400">/</li>
              <li className="text-gray-700">Donation History</li>
            </ol>
          </nav>
        </div>

        {/* Donation History Table Section */}
        <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
          <h2 className="text-xl font-bold text-blue-900 mb-4">Donation History Table</h2>
          
          {/* Table Controls */}
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center gap-2">
              <select
                value={entriesPerPage}
                onChange={(e) => {
                  setEntriesPerPage(Number(e.target.value));
                  setCurrentPage(1);
                }}
                className="px-3 py-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-green-500 focus:border-transparent outline-none"
              >
                <option value={10}>10</option>
                <option value={25}>25</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </select>
              <span className="text-sm text-gray-600">entries per page</span>
            </div>

            <div className="flex items-center gap-2">
              <input
                type="text"
                placeholder="Search..."
                value={filters.search}
                onChange={(e) => {
                  setFilters(prev => ({ ...prev, search: e.target.value }));
                  setCurrentPage(1);
                }}
                className="px-4 py-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-green-500 focus:border-transparent outline-none"
              />
            </div>
          </div>

          {/* Table */}
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="bg-green-100">
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Name Of Donor
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Donor Email
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Donor Mobile No.
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Donation For
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Donation Date
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Amount Donated
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">
                    <div className="flex items-center gap-1">
                      Payment Method
                      <div className="flex flex-col">
                        <BiChevronUp className="text-xs text-gray-400" />
                        <BiChevronDown className="text-xs text-gray-400" />
                      </div>
                    </div>
                  </th>
                </tr>
              </thead>
              <tbody>
                {isLoading ? (
                  <tr>
                    <td colSpan={7} className="px-4 py-8 text-center">
                      <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-green-500 mx-auto"></div>
                      <p className="text-gray-600 mt-2">Loading donations...</p>
                    </td>
                  </tr>
                ) : currentDonations.length > 0 ? (
                  currentDonations.map((donation, index) => (
                    <tr
                      key={donation._id}
                      className={`border-b border-gray-100 ${index % 2 === 0 ? 'bg-white' : 'bg-gray-50'}`}
                    >
                      <td className="px-4 py-3 text-sm text-gray-900">
                        {donation.isAnonymous ? 'Anonymous' : (donation.donorDetails?.name || user?.name || 'N/A')}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700">
                        {donation.isAnonymous ? 'Hidden' : (donation.donorDetails?.email || user?.email || 'N/A')}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700">
                        {donation.isAnonymous ? 'Hidden' : (donation.donorDetails?.phone || user?.phone || 'N/A')}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700">
                        {donation.campaign?.title || 'General Donation'}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700">
                        {new Date(donation.createdAt).toLocaleDateString('en-GB', {
                          day: '2-digit',
                          month: '2-digit',
                          year: 'numeric'
                        })}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-900 font-medium">
                        ‚Çπ{donation.amount?.toLocaleString() || 0}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-700 uppercase">
                        {donation.paymentMethod || donation.paymentDetails?.gateway || 'N/A'}
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={7} className="px-4 py-8 text-center text-gray-500">
                      No entries found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="flex items-center justify-between mt-4 pt-4 border-t border-gray-200">
              <div className="text-sm text-gray-600">
                Showing {startIndex + 1} to {Math.min(endIndex, filteredDonations.length)} of {filteredDonations.length} entries
              </div>
              <div className="flex items-center gap-2">
                <button
                  onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                  disabled={currentPage === 1}
                  className="px-3 py-1 border border-gray-300 rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50"
                >
                  Previous
                </button>
                <span className="text-sm text-gray-600">
                  Page {currentPage} of {totalPages}
                </span>
                <button
                  onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                  disabled={currentPage === totalPages}
                  className="px-3 py-1 border border-gray-300 rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50"
                >
                  Next
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </UserLayout>
  );
}
